.searchbox-container {
  margin: 30px auto 50px;
  text-align: center;
}

.searchbox-container i {
  color: #ff5031;
  position: absolute;
  margin-left: 16px;
  margin-top: 9px;
} 

.searchbox-container input {
  height: 35px;
  width: 50%;
  padding: 14px;
  padding-left: 44px;
  border-radius: 25px;
  border: none;
  background-color: #fafafa;
  box-shadow: -5px -5px 6px #ffffff, 5px 5px 6px #00000016;
}

.box_animation {
  animation-duration: 2s;
}

@media screen and (max-width: 700px) {
  .searchbox-container input {
    width: 90%;
  }
}